import { useSelector } from 'react-redux'
import { convertToTS } from '../../../utils/AppHelper'
import React from 'react'
import BlankTableError from '../../commanComponent/BlankTableError'
import { CustomSpinner } from '../../globalComponents'


const AllAttendanceLog = (props) => {
    const { getCheckInCheckOutPending } = useSelector(state => state.attendanceDashboard)
    const { openLogModel, setOpenLogModel, attendanceAllLog, totalWorkingHours, breakTime, attendanceTime } = props
    const renderAttendanceLogs = () => {
        return (
            attendanceAllLog && attendanceAllLog.length > 0 ? attendanceAllLog.map((item, index) => (
                <tr>
                    <td><span class="srNum">{index + 1}</span></td>
                    <td>{convertToTS(item.punch_time, "HH:mm:ss")}</td>
                    <td>{item.activity_name}</td>
                    <td>{item.log_type}</td>
                </tr>
            )) : (
                 <BlankTableError totalField={4} />
            )
        )
    }

    const tooltipDots = (data)=>{
        return (
            <img
            src="images/tooltipdots.svg"
            className="ms-2"
            alt=""
            width={20}
            data-bs-toggle="tooltip"
            data-bs-placement="top"
            data-bs-title={data}
        />
        )
    }

    return (
        <>
            <div class="modal fade attendanceViewodal" style={{ display: openLogModel ? "unset !important" : "none !important", opacity: openLogModel ? "unset" : "0" }}>
                <div class="modal-dialog modal-dialog-centered">
                    <div class="modal-content rounded-0 border-0">
                        <div class="modal-header border-0">
                            <h1 class="modal-title fs-5 fw-bold text-primary">Attendance Logs
                            </h1>
                            <button type="button" class="btn-close" data-bs-dismiss="modal" aria-label="Close" onClick={() => setOpenLogModel(!openLogModel)}></button>
                        </div>
                        <div class="modal-body">
                            <div class="d-flex align-items-center justify-content-between mb-3">
                                <strong class="fs-7 text-primary"><span class="text-light fw-normal">Date :</span>
                                    {convertToTS(attendanceTime?.date)}</strong>
                                <strong class="fs-7 text-primary">{convertToTS(attendanceTime?.date, "dddd")}</strong>

                            </div>
                            <div class="timeCountmdl">
                                <strong><span>{totalWorkingHours}</span> Working Hours</strong>
                                <strong><span>{breakTime}</span> Break Time </strong>
                            </div>
                            <div class="table-responsive attendanceTbl">
                                <table class="w-100">
                                    <thead>
                                        <tr>
                                            <th width="15%">S.No</th>
                                            <th>Time</th>
                                            <th>Activity Name</th>
                                            <th>IN/OUT</th>
                                        </tr>
                                    </thead>
                                    <tbody>
                                        {getCheckInCheckOutPending ?
                                            <tr> <td colSpan={4}>
                                                <CustomSpinner isShow={getCheckInCheckOutPending} size="xl" />
                                                </td></tr>
                                            : renderAttendanceLogs()}

                                    </tbody>
                                </table>
                            </div>
                        </div>

                    </div>
                </div>
            </div>
            {openLogModel && <div className="modal-backdrop fade show"></div>}
        </>
    )
}

export default AllAttendanceLog