import React, { useEffect, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import ProfileImage from "./ProfileImage";
import Cookies from 'js-cookie';
import { authActions } from "../core/resource/actions";
import ApproveRejectModal from "./modal/ApproveRejectModal";
import { useNavigate } from "react-router-dom";
import { getLocalDataAsObject } from "../utils/AppHelper";
import { ResetPasswordModal } from "./modal/auth";
import { useCustomTranslation } from "../translation";


const Header = () => {
  const dispatch = useDispatch();
  const navigate = useNavigate();
  const { t } = useCustomTranslation()
  const {
    logoutPending,
    logoutRes,
    logoutError,

    resetPasswordPending,
    resetPasswordRes,
    resetPasswordError,
  } = useSelector(state => state.auth);
  const data = null;

  const [isShowLogoutModal, setIsShowLogoutModal] = useState(false)
  const [isShowResetPassModal, setIsShowResetPassModal] = useState(false)
  const [userData, setUserData] = useState('')


  useEffect(() => {
    getUserData();
  }, [])


  const getUserData = async () => {
    const user = await getLocalDataAsObject("user");
    setUserData(user)
  };


  /* Logout */
  const toggleLogoutModal = () => {
    setIsShowLogoutModal(!isShowLogoutModal)
  }
  const handleLogout = (type) => {
    if (type) {
      dispatch(authActions?.logoutRequest())
    } else {
      setIsShowLogoutModal(!isShowLogoutModal)
      dispatch(authActions?.handleClearAuthStore("HANDLE_CLEAR_LOGOUT_STORE"))
    }
  }
  useEffect(() => {
    if (!logoutPending) {
      if (logoutError) {
        dispatch(authActions?.handleClearAuthStore("HANDLE_CLEAR_LOGOUT_STORE"))
        return;
      }
      if (logoutRes) {
        if (logoutRes?.code === 200) {
          dispatch(authActions?.handleClearAuthStore("HANDLE_CLEAR_LOGOUT_STORE"))
          localStorage.clear();
          sessionStorage.clear();
          Cookies.remove('authtoken')
          Cookies.remove('authData')
          setIsShowLogoutModal(!isShowLogoutModal)
          navigate('/login')
          window.location.reload();
        }
      }
    }
  }, [
    logoutRes,
    logoutError,
    logoutPending
  ]);

  /* Change Password */
  const toggleResetPasswordModal = (params) => {
    if (params) {
      dispatch(authActions.resetPasswordRequest(params))
    }
    else {
      setIsShowResetPassModal(!isShowResetPassModal)
    }
  }
  useEffect(() => {
    if (!resetPasswordPending) {
      if (resetPasswordError) {
        dispatch(authActions?.handleClearAuthStore("HANDLE_CLEAR_RESET_PASSWORD_STORE"))
        return;
      }
      if (resetPasswordRes) {
        if (resetPasswordRes?.code === 200) {
          dispatch(authActions?.handleClearAuthStore("HANDLE_CLEAR_RESET_PASSWORD_STORE"))
          toggleResetPasswordModal()
          navigate('/login')
          // window.location.reload();
        }
      }
    }
  }, [
    resetPasswordRes,
    resetPasswordError,
    resetPasswordPending
  ]);


  return (
    <>
      <header className="mainHead">
        <div className="container-fluid">
          <div className="d-flex align-items-center flex-wrap justify-content-between">
            <span href="#">
              <img src="images/logo.png" alt="" width={110} />
            </span>
            <div className="topRightInner">
              <div className="searchBox">
                <input
                  type="text"
                  className="form-control"
                  placeholder="Search here.."
                />
                <button type="button" className="searchButton">
                  {" "}
                  <img src="images/search-icon.svg" alt="" width={30} />
                </button>
              </div>
              <div className="notficationButton">
                <span href="#">
                  <img src="images/notification-icon.svg" alt="" width={35} />
                </span>
              </div>
              <div className="dropdown priofileDropdown">
                <button
                  className="dropdown-toggle"
                  type="button"
                  data-bs-toggle="dropdown"
                  aria-expanded="false"
                >
                  {userData && userData?.name}
                  <ProfileImage />{" "}
                  {/* {`${data?.data[0].first_name} ${data?.data[0].last_name}`} */}
                </button>
                <ul className="dropdown-menu">
                  <li>
                    <span className="dropdown-item" href="#;">
                      My Profile
                    </span>
                  </li>
                  <li>
                    <span className="dropdown-item"
                      onClick={() => toggleResetPasswordModal()}
                    >Change Password</span>
                  </li>
                  <li>
                    <span
                      className="dropdown-item"
                      onClick={() => toggleLogoutModal()}
                    >
                      Log Out
                    </span>
                  </li>
                </ul>
              </div>
            </div>
          </div>
        </div>
      </header>
      {isShowLogoutModal && (
        <ApproveRejectModal
          onApproveReject={handleLogout}
          isShow={isShowLogoutModal}
          desc={"You want to Logout"}
          btnRightLoading={logoutPending}
          btnTextRight={t("btns.logout")}
          btnTextLeft={t("btns.cancel")}
        />)
      }
      {isShowResetPassModal && <ResetPasswordModal
        isShow={isShowResetPassModal}
        toggleResetPasswordModal={toggleResetPasswordModal}
      />}
    </>
  );
};

export default Header;
